.m-section{
  padding: 5em 0;
}

.portfolio{
  padding-left: 3.75em;
  padding-right: 3.75em;
  overflow: hidden;
  &__top{
    display: flex;
    justify-content: center;
  }
  &__text{
    padding-bottom: .875em;
    position: relative;
  }
  &__line{
    position: absolute;
    height: .5625em;
    display: block;
    bottom: 0;
    left: 50%;
    transform: translateX(-50%);
    &._view2{
      display: none;
    }
  }
  &__star{
    position: absolute;
    top: -1.63em;
    right: -3.125em;
    width: 5.125em;
  }
  &__groups{
    margin-top: 2.125em;
    @include grid100gap(1.5em);
  }
  &__group{
    display: flex;
    align-items: stretch;
    &:hover .portfolio__items{
      animation-play-state: paused;
    }

    &:nth-child(even){
      flex-direction: row-reverse;
      .portfolio__items{
        animation-name: slides2;
      }
    }
    .portfolio-card{
      margin-right: 1.5em;
    }
  }
  &__items{
    flex-shrink: 0;
    display: inline-flex;
    align-items: stretch;
    animation: 15s slides infinite linear;
  }
  &__nav{
    margin-top: 5em;
    display: flex;
    justify-content: center;
    .m-btn{
      svg{
        margin-left: 1.69em;
      }
    }
  }
}

@keyframes slides {
  from {
    transform: translateX(0);
  }
  to {
    transform: translateX(-100%);
  }
}

@keyframes slides2 {
  from {
    transform: translateX(0);
  }
  to {
    transform: translateX(100%);
  }
}

.portfolio-card{
  border-radius: 1.5em;
  background: $bg;
  backdrop-filter: blur(5px);
  width: 18.75em;
  flex-shrink: 0;
  padding: 1.25em;
  display: block;
  &:hover{
    cursor: pointer;
    color: $black;
    .portfolio-card__img img{
      opacity: 0.25;
    }
    .portfolio-card__link{
      opacity: 1;
    }
  }
  &__top{
    position: relative;
  }
  &__img{
    border-radius: 1em;
    background: #FFF;
    height: 11.25em;
    @include center;
    padding: .875em;
    img{
      transition: all .3s;
      max-width: 100%;
      max-height: 100%;
    }
  }
  &__link{
    @include centerAbsolute;
    width: 4.25em;
    height: 4.25em;
    border-radius: 50%;
    opacity: 0;
  }
  &__bottom{
    margin-top: 2.5em;
  }
}

.map{
  &__inner{
    background-color: $bg;
    border-radius: 1.5em;
    padding: 2.5em;
    position: relative;
  }
  &__wrap{
    position: relative;
  }
  &__items{
    padding: 5.25em 0 8.25em;
    @include grid100gap(3em);
  }
  &__lines{
    position: absolute;
    top: 0;
    right: 0;
    width: 68.125em;
    svg{
      width: 100%;
    }
    &-fill{
      position: absolute;
      top: 0;
      right: 0;
      width: 100%;
      height: 0;
      max-height: 100%;
      overflow: hidden;
      transition: all .3s;
    }
    ._view2{
      display: none;
    }
  }
  &__top{
    position: absolute;
    top: 2.5em;
    left: 2.5em;
    height: calc(100% - 32em);
    width: 41.5em;
  }
  &__text{
    position: sticky;
    top: 8em;
  }
  &__line{
    position: absolute;
    top: 100%;
    left: 0;
    margin-top: 1.59em;
    width: 10.6875em;
    &._view2{
      display: none;
    }
  }
}

.map-card{
  padding: 1.25em;
  border-radius: 1.5em;
  background: #FFF;
  width: 33.5em;
  margin-left: auto;
  height: 17.5625em;
  display: flex;
  flex-direction: column;
  &:nth-child(even){
    margin-right: 1.44em;
  }
  &._finish{
    margin: 0;
    background-color: $red;
    .m-badge{
      background-color: #fff;
    }
  }
  &__badges{
    display: flex;
    flex-wrap: wrap;
    margin-top: -.5em;
    .m-badge{
      margin: .5em .5em 0 0;
    }
  }
  &__bottom{
    margin-top: auto;
    padding-top: 1em;
    display: flex;
    align-items: flex-end;
    justify-content: space-between;
  }
  &__num{
    font-size: 5em;
    line-height: 110%;
    font-weight: 700;
  }
}

.m-badge{
  height: 2.1875em;
  padding: 0 .75em;
  border-radius: 0.5em;
  background-color: $bg;
  display: flex;
  align-items: center;
  span{
    font-size: 1em;
    line-height: 120%;
  }
}


@media screen and (max-width: 992px){
  .m-section{
    padding: 3.125em 0;
  }

  .portfolio{
    &__text{
      padding-bottom: .5625em;
    }
    &__line{
      &._view1{
        display: none;
      }
      &._view2{
        display: block;
      }
    }
    &__star{
      top: -.6875em;
      right: -1.125em;
      width: 2.125em;
    }
    &__groups{
      margin-top: 1.44em;
      gap: .875em;
    }
    &__group{
      .portfolio-card{
        margin-right: .875em;
      }
    }
    &__nav{
      margin-top: 2em;
      .m-btn{
        svg{
          display: none;
        }
      }
    }
  }

  .portfolio-card{
    padding: .75em;
    width: 9.375em;
    &__img{
      height: 6.25em;
    }
    &__bottom{
      margin-top: 2.31em;
    }
  }

  .map{
    @media screen and (min-width: 577px){
      &__inner{
        font-size: .5em;
      }
      &__text{
        top: 14em;
      }
      .m-title{
        font-size: 3.75em;
      }
    }
  }
}


@media screen and (max-width: 576px){
  .map{
    &__inner{
      padding: .75em;
      border-radius: 1em;
    }
    &__top{
      width: 100%;
      height: auto;
      left: unset;
      top: unset;
      position: relative;
    }
    &__line{
      width: 4.25em;
      left: unset;
      right: 2.12em;
      top: unset;
      bottom: -2.38em;
      &._view1{
        display: none;
      }
      &._view2{
        display: block;
      }
    }
    &__lines{
      width: 21.625em;
      right: -.375em;
      top: -.75em;
      ._view1{
        display: none;
      }
      ._view2{
        display: block;
      }
    }
    &__wrap{
      margin-top: 2em;
    }
    &__items{
      padding: 1.5em 0 1.75em;
      gap: 1.75em;
    }
  }

  .map-card{
    border-radius: 1em;
    padding: .75em;
    width: calc(100% - .875em);
    &__num{
      font-size: 3.75em;
    }
    &._finish{
      margin-top: 4.81em;
    }
  }

  .m-badge{
    min-height: 2.0625em;
    height: unset;
    padding: .5em .75em;
    span{
      font-size: .875em;
    }
  }
}
