.blog-main{
  &__inner{
    padding: 16.37em 2.5em 2.5em;
    background-color: $red;
    background-image: url(../img/bg5.svg);
    background-size: cover;
    background-position: center;
    color: #fff;
    border-radius: 1.5em;
    min-height: 26em;
    display: flex;
    align-items: flex-end;
  }
  &__content{
    display: flex;
    flex-direction: column;
    align-items: flex-start;
  }
  &__title{
    display: flex;
    align-items: flex-end;
  }
  &__pencil{
    width: 6.1875em;
    margin: -1.0625em 0 0 3em;
  }
  &__back{
    display: flex;
    align-items: center;
    margin-bottom: 1.5em;
    color: #fff;
    opacity: .7;
    &:hover{
      color: #fff;
      opacity: 1;
    }
    img{
      width: 1.5em;
      margin-right: .75em;
    }
    span{
      font-size: 1em;
      line-height: 120%;
    }
  }
}

.blog-items{
  padding: 6.25em 0 5em;
  &__nav{
    margin-top: 3em;
    display: flex;
    justify-content: center;
  }
}

.article{
  padding: 6.25em 0 5em;
  &__inner{
    display: grid;
    grid-template-columns: calc(100% - 28em) 22em;
    gap: 8.5em;
  }
}

.article-nav{
  position: sticky;
  top: 8em;
  border-radius: 1.5em;
  padding: 1.5em;
  background-color: $bg;
  &__group{
    padding: 1em 0;
    border-bottom: $border;
    @include grid100gap(.5em);
    &:first-child{
      padding-top: 0;
    }
    &:last-child{
      padding-bottom: 0;
      border-bottom: none;
    }
  }
  &__links{
    display: flex;
    flex-wrap: wrap;
    margin-top: -.25em;
    .m-btn{
      flex-shrink: 0;
      margin: .5em .5em 0 0;
      height: 2em;
      padding: 0 .75em;
      border: none;
      span{
        font-size: .875em
      }
      .icon{
        margin-right: .5em;
      }
    }
  }
  &__star{
    position: absolute;
    top: -1.75em;
    right: 1.06em;
    width: 4.125em;
  }
}

.article-text{
  font-weight: 300;
  line-height: 150%;
  img{
    width: 100%;
    display: block;
    border-radius: 1.5em;
    margin-bottom: 2em;
    &:last-child{
      margin-bottom: 0;
    }
  }
  hr{
    height: 2em;
    opacity: 0;
    display: block;
  }
  h2,h3,h4,h5,h6{
    line-height: 120%;
    font-weight: 700;
  }
  h2{
    font-size: 3em;
    @include lastM(0 0 .5em);
  }
  h3{
    font-size: 2.5em;
    @include lastM(0 0 .8em);
  }
  h4{
    font-size: 2em;
    @include lastM(0 0 .75em);
  }
  h5{
    font-size: 1.5em;
    @include lastM(0 0 1em);
  }
  h6{
    font-size: 1.125em;
    @include lastM(0 0 .89em);
  }
  li,
  p{
    font-size: .9375em;
    line-height: 150%;
  }
  p{
    @include lastM(0 0 2.13em)
  }
  mark{
    background-color: transparent;
    color: $red;
    font-weight: 700;
  }
  ul{
    @include lastM(0 0 2em);
    @include grid100gap(1.25em);
    li{
      position: relative;
      padding-left: 2.13em;
      &:before{
        content: '';
        position: absolute;
        top: .133em;
        left: 0;
        width: 1.066em;
        height: 1.066em;
        background-image: url(../img/marker.svg);
        @include bgImgSize(100%);
      }
    }
  }
  .slider,
  .gallery{
    margin: 3em 0;
    &:last-child{
      margin-bottom: 0;
    }
  }
  blockquote{
    margin: 3em 0;
    padding: 3em 3em 3em 12.5em;
    border-radius: 1.5em;
    background-color: $bg;
    position: relative;
    &:before{
      content: '';
      position: absolute;
      width: 3.75em;
      height: 3.75em;
      top: 3em;
      left: 3em;
      background-image: url(../img/star3.svg);
      @include bgImgSize(100%);
    }
  }
  .slider{
    position: relative;
    .swiper{
      &-container{
        overflow: hidden;
        border-radius: 1em;
      }
      &-wrapper{
        align-items: stretch;
      }
      &-slide{
        height: auto;
        width: calc(100% + 1em);
        padding-right: 1em;
      }
      &-nav{
        position: absolute;
        bottom: 1.5em;
        right: 1.5em;
        display: flex;
      }
      &-button{
        margin: 0 0 0 .75em;
        position: relative;
        top: unset;
        left: unset;
        right: unset;
        width: 4.5em;
        height: 3.75em;
        padding: 0;
      }
    }
    img{
      margin: 0;
      min-height: 100%;
      border-radius: 1em;
      object-fit: cover;
    }
  }
}

.gallery{
  display: grid;
  grid-template-columns: repeat(2,calc(50% - .75em));
  gap: 1.5em;
  img{
    min-height: 100%;
    object-fit: cover;
    margin: 0;
  }
}


@media screen and (max-width: 992px){
  .blog-main{
    &__inner{
      border-radius: 1em 1em 0 0;
      min-height: 14.8125em;
      padding: 10.125em .75em 1em;
    }
    &__pencil{
      width: 3.625em;
      margin: -.9375em 0 0 .8125em;
    }
    &__back{
      margin-bottom: .75em;
      span{
        font-size: .875em;
      }
    }
  }

  .blog-items{
    padding: 3.25em 0 3.125em;
    &__nav{
      margin-top: 2em;
    }
  }

  .article{
    padding: 3.25em 0 3.125em;
    overflow: hidden;
    &__inner{
      grid-template-columns: 100%;
      gap: 3em;
    }
  }

  .article-text{
    img{
      margin-bottom: 1.5em;
    }
    h2{
      font-size: 1.75em;
      @include lastM(0 0 .57em);
    }
    h3,h4{
      font-size: 1.3125em;
      @include lastM(0 0 1.14em);
    }
    h5{
      font-size: 1.125em;
      @include lastM(0 0 .89em);
    }
    p,li{
      font-size: .8125em;
    }
    p{
      @include lastM(0 0 1.6em);
    }
    hr{
      height: 1.5em;
    }
    .gallery{
      margin: 2em 0;
    }
    blockquote{
      margin: 2em 0;
      padding: .75em .75em .75em 5em;
      &:before{
        top: .75em;
        left: .75em;
      }
    }
    .slider{
      margin: 2em 0;
      .swiper{
        &-container{
          overflow: unset;
        }
        &-slide{
          width: calc(100% - .875em);
          padding-right: .875em;
          min-height: 18.75em;
        }
        &-nav{
          display: none;
        }
      }
    }
  }

  .gallery{
    grid-template-columns: repeat(2,calc(50% - .4375em));
    gap: .875em;
  }
}


@media screen and (max-width: 576px){
  .article-nav{
    padding: .75em;
    &__star{
      right: -.44em;
    }
  }
}
