.header{
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  z-index: 30;
  &__inner{
    padding: 3.75em 2.5em 1em;
    position: relative;
    transition: all .3s;
  }
  &__logo{
    display: block;
    width: 11.125em;
    position: absolute;
    top: 0;
    left: 50%;
    transform: translateX(-50%);
    border-radius: 0 0 1.5em 1.5em;
    border: $border2;
    background: #FFF;
    padding: 2.19em 2.62em;
    img{
      width: 100%;
      display: block;
    }
  }
  &__call{
    margin-right: 1em;
    svg{
      display: none;
    }
  }
  &._fix{
    @media screen and (min-width: 993px){
      .header{
        &__logo{
          padding: 1em 1.5em;
          width: 7.125em;
        }
        &__inner{
          padding-top: 3.375em;
        }
      }
    }
  }
}

._flex{
  display: flex;
  align-items: center;
}

.menu{
  flex-grow: 1;
}

.nav{
  flex-grow: 1;
  justify-content: space-between;
}

.lang{
  margin-left: 1em;
  &__title{
    display: none;
  }
}

.burger{
  display: none;
}


@media screen and (max-width: 992px){
  .header{
    .container{
      padding: 0;
    }
    &__inner{
      padding: .75em;
      justify-content: space-between;
    }
    &__logo,
    &__call,
    .burger{
      z-index: 21;
    }
    &__logo{
      width: 6.25em;
      padding: 1.24em 1.47em;
    }
    &__call{
      margin: 0;
      .text{
        display: none;
      }
      svg{
        display: block;
      }
    }
    &._open{
      .burger{
        background: $red;
        ._view1{
          display: none;
        }
        ._view2{
          display: block;
        }
      }
      .menu{
        transform: translateX(0);
      }
    }
    &._fix{
      .header{
        &__logo{
          font-size: .875em;
        }
      }
    }
  }

  .menu{
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: $vh;
    transform: translateX(-120%);
    transition: all .4s;
    z-index: 20;
    background-color: $bg;
    padding: 10.38em 0 0;
    flex-direction: column;
    align-items: unset;
    overflow-y: auto;
  }

  .nav{
    flex-grow: unset;
    padding: 1.5em .125em;
    border-top: $border;
    border-bottom: $border;
    width: calc(100% - 2em);
    margin: 0 auto 2em;
    flex-direction: row-reverse;
    justify-content: flex-end;
    &__link{
      padding: 0 1em;
      &:not(:first-child){
        margin-right: .75em;
      }
    }
  }

  .burger{
    display: flex;
    ._view2{
      display: none;
    }
  }

  .lang{
    margin: auto 0 0;
    background: #FFF;
    padding: .75em 1em;
    justify-content: space-between;
    &__title{
      display: block;
    }
    &__btn{
      &:not(:hover){
        background-color: $bg;
      }
    }
  }
}



@media screen and (max-width: 576px){
  .header{
    &._fix{
      .header{
        &__logo{
          font-size: .625em;
        }
      }
    }
  }
}
