@import "../vars";
.mbtn {
    display: flex;
    align-items: center;
    justify-content: center;
    transition: all ease-out .225s;
    padding: 0;
    &:focus {
        box-shadow: none;
    }
}

.m-btn {
    @include center;
    box-sizing: border-box;
    padding: 0 1.25em;
    height: 3.25em;
    border-radius: 1.5em;
    position: relative;
    overflow: hidden;
    color: $black;
    border: 1px solid $bg;
    background: #FFF;
    & span {
        //mix-blend-mode: difference;
    }
    &:before {
        content: "";
        height: 0;
        aspect-ratio: 2;
        transform: translateX(-50%);
        position: absolute;
        top: -200%;
        left: 50%;
        background: $red;
        border-radius: inherit;
        transition: height 0.3s;
    }
    & .text-container {
        @include center;
        position: relative;
        overflow: hidden;
        transform: translateY(7%);
    }
    .icon{
        @include center;
    }
    & .text {
        display: block;
        position: relative;
        font-size: .875em;
        line-height: 120%;
        //mix-blend-mode: difference;
    }
    img,
    svg{
        width: 1.5em;
        transform: translateY(-7%);
    }
    &:hover {
        color: #fff;
        border-color: $red;
        background-color: $red;
        .icon,
        .text {
            animation: move-up-alternate 0.3s forwards;
            will-change: transform;
        }
        &:before {
            height: 400%;
        }
        rect{
            fill: #fff;
        }
        ._stroke{
            stroke: #fff;
        }
        path:not(._stroke){
            fill: #fff;
        }
    }

    @keyframes move-up-alternate {
        from {
            transform: translateY(0%);
        }
        50% {
            transform: translateY(80%);
        }
        51% {
            transform: translateY(-80%);
        }
        100% {
            transform: translateY(0%);
        }
    }
}

.m-btn-big{
    @media screen and (min-width: 993px){
        padding: 0 2em;
        height: 4.25em;
        .text{
            font-size: 1em;
        }
    }
}

.m-btn-red{
    background: $red;
    border: none;
    &:before{
        background: #fff;
    }
    &:hover{
        background-color: #fff;
        color: $red;
        ._stroke{
            stroke: $red;
        }
    }
}

.m-btn-red2{
    background: $red;
    border: none;
    color: #fff;
}

.m-btn-white,
.m-btn-red2{
    &:before{
        background: $black;
    }
    &:hover{
        background-color: $black;
        color: #fff;
    }
}

.m-btn-gray{
    &:not(:hover){
        background-color: $bg;
    }
}

.m-btn-black{
    background: $black;
    border: none;
    color: #fff;
    &:before{
        background: $red;
    }
    &:hover{
        background-color: $red;
        color: #fff;
    }
}
