.footer{
  margin-top: 5em;
  color: #fff;
  &__inner{
    border-radius: 1.5em;
    overflow: hidden;
    display: grid;
    grid-template-columns: 50% 50%;
  }
  &__media{
    position: relative;
  }
  &__logo{
    display: block;
    position: absolute;
    top: 2.5em;
    left: 2.5em;
    width: 5.125em;
    img{
      width: 100%;
      display: block;
    }
  }
  &__img{
    display: block;
    width: 100%;
    min-height: 100%;
    object-fit: cover;
  }
  &__content{
    background-color: $red;
    padding: 2.5em;
    display: flex;
    flex-direction: column;
  }
  &__links{
    margin-top: 1.5em;
  }
  &__link{
    background-color: transparent;
    border: none;
    border-top: 1px solid rgba(255, 255, 255, 0.30) !important;
    justify-content: space-between;
    border-radius: 0;
    font-weight: 700;
    color: #fff;
    padding: 1.31em 0;
    height: unset;
    &:last-child{
      border-bottom: 1px solid rgba(255, 255, 255, 0.30) !important;
    }
    .text{
      font-size: 2em;
      line-height: 120%;
    }
  }
  &__bottom{
    padding-top: 1.5em;
    margin-top: auto;
  }
}

.footer-contacts{
  height: 5em;
  display: flex;
  align-items: center;
  justify-content: space-between;
  border-bottom: 1px solid rgba(255, 255, 255, 0.30);
  position: relative;
  &:before{
    content: '';
    position: absolute;
    height: 100%;
    width: 1px;
    background-color: rgba(255, 255, 255, 0.30);
    top: 0;
    left: 50%;
    transform: translateX(-50%);
  }
  &__link{
    &:hover{
      color: $graySup;
    }
    span{
      font-size: 1em;
      line-height: 120%;
    }
  }
}

.footer-hint{
  display: flex;
  align-items: flex-start;
  &__img{
    width: 3.0625em;
    margin-right: 1em;
  }
  &__glass{
    margin-top: .41em;
    margin-left: auto;
    width: 4.9375em;
    transform: rotate(-15deg);
  }
}

.footer-contacts2{
  margin-top: 1.25em;
  display: flex;
  justify-content: space-between;
  &__doc{
    height: 3.75em;
  }
}

.footer-socials{
  &__title{
    text-align: right;
  }
  &__items{
    display: flex;
    margin-top: .75em;
  }
  &__item{
    height: 2em;
    display: flex;
    align-items: center;
    padding: 0 .75em;
    border-radius: 1.5em;
    background: rgba(255, 255, 255, 0.30);
    margin-left: .75em;
    color: #fff;
    span{
      font-size: .875em;
      line-height: 120%;
    }
    &:hover{
      background-color: #fff;
      color: $black;
    }
  }
}


@media screen and (max-width: 992px){
  .footer{
    margin-top: 3.125em;
    &__inner{
      border-radius: 1em;
    }
    &__logo{
      top: .75em;
      left: .75em;
      width: 4.1875em;
    }
    &__content{
      padding: .75em;
    }
    &__links{
      margin-top: 1em;
    }
    &__link{
      padding: .75em 0;
      .text{
        font-size: 1.5em;
      }
    }
  }

  .footer-contacts{
    height: auto;
    flex-direction: column;
    align-items: flex-start;
    padding: .75em 0;
    &:before{
      display: none;
    }
    &__link{
      @include lastM(0 0 .5em);
      span{
        font-size: .875em;
      }
    }
  }

  .footer-hint{
    &__glass{
      display: none;
    }
  }

  .footer-contacts2{
    margin-top: 1em;
    @include grid100gap(1.5em);
    &__doc{
      height: 3.25em;
    }
  }

  .footer-socials{
    &__title{
      text-align: left;
    }
    &__items{
      margin-top: .5em;
    }
    &__item{
      margin: 0 .75em 0 0;
    }
  }
}


@media screen and (max-width: 576px){
  .footer{
    &__inner{
      grid-template-columns: 100%;
    }
    &__content{
      order: -1;
    }
  }
}
