@import 'vars';
html {
    width: 100%;
    &.fixed-body {
        overflow-y: hidden;
    }
}

.wrapper {
    min-height: 100vh;
    display: flex;
    flex-direction: column;
    flex-shrink: 0;
}

.content {
    flex-grow: 1;
    display: flex;
    flex-direction: column;
    width: 100%;
}

.m-page {
    width: calc(100vw - (100vw - 100%));
    overflow-x: hidden;
    font-family: $font1;
    font-weight: 400;
    color: $black;
    font-size: calc(100vw * (16 / 1440));
    padding: 1.25em 0;
    @media screen and (min-width: 1440px){
        font-size: calc(1440px * (16 / 1440));
    }
    &.fixed-body,
    &.fixed-body2{
        overflow-y: hidden;
        padding-right: var(--scrollWidth);
    }
}

.container {
    max-width: 1920px;
    width: 100%;
    margin: 0 auto;
    padding: 0 1.25em;
    &._small{
        @media screen and (min-width: 993px){
            padding: 0 3.75em;
        }
    }
}

a {
    &:hover {
        color: $red;
    }
}


@media screen and (max-width: 992px){
    .m-page{
        padding: .25em 0;
        font-size: calc(100vw * (16 / 768));
    }

    .container{
        padding: 0 1em;
        &._big-mob{
            padding: 0 .25em;
        }
    }
}


@media screen and (max-width: 576px){
    .m-page{
        font-size: calc(100vw * (16 / 360));
    }
}


