.main{
  margin-bottom: 5em;
  &__inner{
    display: grid;
    grid-template-columns: 50% 50%;
    min-height: calc(100 * var(--vh) - 2.5em);
    position: relative;
    border-radius: 1.5em;
    overflow: hidden;
    background-color: $bg;
  }
  &__left,
  &__right{
    background-size: cover;
    background-position: center;
    padding: 2.5em;
    display: flex;
    align-items: flex-end;
  }
  &__left{
    background-color: $red;
    color: #fff;
    background-image: url(../img/bg.svg);
    position: relative;
  }
  &__right{
    background-image: url(../img/bg2.svg);
  }
  &__text{
    @include grid100gap(1.25em);
    position: relative;
    z-index: 2;
  }
  &__name{
    font-size: 5em;
    line-height: 110%;
    font-weight: 700;
    text-transform: uppercase;
  }
  &__img{
    position: absolute;
    bottom: 0;
    right: 0;
    width: calc(100% - 9.31em);
    max-width: 40em;
    display: block;
  }
  &__item{
    padding: 1.5em 0;
    border-bottom: $border;
    &:first-child{
      border-top: $border;
    }
  }
}

.experience{
  &__top{
    position: relative;
  }
  .m-title{
    position: relative;
    z-index: 2;
  }
  &__lines{
    top: -1em;
    left: -1.44em;
    position: absolute;
    width: 17.8125em;
    &._view2{
      display: none;
    }
  }
  &__grid{
    margin-top: 3em;
    display: grid;
    grid-template-columns: repeat(4,calc(25% - 1.125em));
    gap: 1.5em;
  }
  &__media{
    grid-column: 3/-1;
    grid-row: 1/3;
    position: relative;
  }
  &__star{
    position: absolute;
    top: -2.9375em;
    right: -2.0625em;
    width: 7.875em;
  }
  &__img{
    height: 100%;
    img{
      display: block;
      width: 100%;
      min-height: 100%;
      object-fit: cover;
      border-radius: 1.5em;
    }
  }
}

.experience-card{
  padding: 1.25em;
  border-radius: 1.5em;
  border: $border;
  min-height: 16.5em;
  display: flex;
  flex-direction: column;
  @media screen and (min-width: 577px){
    &._big{
      grid-column: span 2;
      .experience-card__top{
        color: $red;
      }
      .experience-card__num{
        font-size: 8.75em;
      }
      .experience-card__val{
        font-weight: 700;
        .text18{
          font-size: 3.75em;
        }
      }
    }
  }
  &__top{
    display: flex;
  }
  &__num{
    font-size: 5em;
    line-height: 110%;
    font-weight: 700;
  }
  &__val{
    margin-left: .5em;
  }
  &__text{
    margin-top: auto;
    padding-top: 1em;
  }
}

.for-what{
  &__inner{
    padding: 8.75em 15em 4.5em;
    border-radius: 1.5em;
    background: $bg;
    display: flex;
    flex-direction: column;
    align-items: center;
    position: relative;
  }
  &__text{
    text-align: center;
    max-width: 53em;
    @include grid100gap(1.5em);
  }
  &__link{
    width: 8.75em;
    height: 8.75em;
    border-radius: 50%;
    margin-top: -1.25em;
  }
  &__img{
    position: absolute;
    pointer-events: none;
  }
  ._img1{
    top: 3.63em;
    left: 4.62em;
    width: 9.5em;
  }
  ._img2{
    right: 2.51em;
    bottom: 2.66em;
    width: 11.24em;
  }
}


@media screen and (max-width: 992px){
  .parallax-item{
    transform: unset !important;
  }

  .main{
    margin-bottom: 3.125em;
    .container{
      padding: 0 .25em;
    }
    &__inner{
      min-height: 30em;
      border-radius: 1em;
    }
    &__left,
    &__right{
      padding: .75em 1em;
    }
    &__left{
      overflow: hidden;
    }
    &__img{
      width: 23.1875em;
      right: -3.5em;
      bottom: -5.5em;
    }
    &__text{
      gap: .75em;
    }
    &__name{
      font-size: 2.5em;
    }
    &__item{
      padding: 1em 0;
    }
  }

  .experience{
    &__lines{
      width: 8.5em;
      left: -.25em;
      top: -.25em;
      &._view1{
        display: block;
      }
    }
    &__grid{
      margin-top: 2em;
      grid-template-columns: repeat(2,calc(50% - .4375em));
      gap: .875em;
    }
    &__media{
      grid-column: 1/-1;
      grid-row: unset;
    }
    &__star{
      top: -.69em;
      right: -.9375em;
      width: 4.625em;
    }
  }

  .for-what{
    &__inner{
      padding: 6.25em .75em 1.81em;
    }
    &__text{
      gap: 1em;
    }
    &__link{
      margin-top: -.5em;
      width: 5em;
      height: 5em;
    }
    ._img2{
      display: none;
    }
    ._img1{
      left: 50%;
      transform: translateX(-90%) !important;
      width: 5.625em;
      top: -1.44em;
    }
  }
}


@media screen and (max-width: 576px){
  .main{
    &__inner{
      min-height: unset;
      grid-template-columns: 100%;
    }
    &__left{
      min-height: 30em;
      background-image: url(../img/bg3.svg);
    }
    &__right{
      background-image: url(../img/bg4.svg);
    }
  }

  .experience{
    &__grid{
      grid-template-columns: 100%;
    }
    &__img{
      img{
        border-radius: 1em;
      }
    }
  }

  .experience-card{
    border-radius: 1em;
    padding: .75em;
    min-height: 11.875em;
    &__num{
      font-size: 3.75em;
    }
    &__val{
      font-weight: 700;
    }
  }
}
