._open-modal{
    .wrapper{
        filter: blur(50px);
    }
}

.modal {
    z-index: 30;
    opacity: 0;
    pointer-events: none;
    transition: all .3s;
    position: fixed;
    width: 100%;
    height: $vh;
    overflow: hidden;
    &__bg{
        background-color: rgba($black,.25);
        position: fixed;
        top: 0;
        left: 0;
        width: 100%;
        height: 100%;
    }
    &__dialog{
        z-index: 10;
        position: fixed;
        bottom: 0;
        right: 0;
        width: 56.25em;
        height: 100%;
        padding: 3em;
        background-color: #fff;
        border-radius: 1.5em 0 0 1.5em;
        overflow-y: auto;
        transition: all .4s;
        transform: translateX(120%);
    }
    &__close{
        position: fixed;
        top: 1.5em;
        right: 57.625em;
        width: 4.5em;
        height: 3.75em;
        padding: 0;
        z-index: 10;
    }
    &._active{
        opacity: 1;
        pointer-events: unset;
        .modal__dialog{
            transform: translate(0%);
        }
    }
    .footer__links{
        margin-top: 1.5em;
    }
    .footer__link{
        color: $black;
        border-top: $border !important;
        &:hover{
            background-color: transparent;
            &:before{
                display: none;
            }
        }
        &:last-child{
            border-bottom-color: $graySup !important;
        }
    }
    .footer-contacts{
        border-color: $graySup;
        &:before{
            background: $graySup;
        }
    }
}

.client-card{
    padding: 1.5em;
    border-radius: 1.5em;
    background-color: $bg;
    display: grid;
    grid-template-columns: calc(100% - 17.75em) 16.25em;
    gap: 1.5em;
    &__img{
        padding: 1em;
        height: 11.25em;
        min-height: 100%;
        @include center;
        border-radius: 1em;
        background-color: #fff;
        img{
            width: 100%;
        }
    }
    &__content{
        display: flex;
        flex-direction: column;
    }
    &__name{
        text-transform: uppercase;
    }
    &__title{
        padding-top: 2em;
        margin-top: auto;
    }
}

.about-client{
    margin-top: 3em;
}

.client-gallery{
    margin-top: 3em;
    &__grid{
        display: grid;
        grid-template-columns: repeat(4,calc(25% - .75em / 4));
        gap: .25em;
        margin-top: 1.5em;
    }
    &__item{
        @include imgCover(100%);
        border-radius: .75em;
    }
}

.text-modal{
    &__num{
        width: 3.75em;
        height: 3.75em;
        background-color: $red;
        border-radius: 50%;
        @include center;
        color: #fff;
        margin-bottom: 1.5em;
        span{
            font-size: 1.5em;
            line-height: 120%;
        }
    }
}

.modal2{
    @media screen and (min-width: 993px){
        .modal{
            &__dialog{
                border-radius: 1.5em 1.5em 0 0;
                width: 100%;
                height: auto;
                max-height: calc(100% - 5.25em);
                transform: translateY(120%);
                overflow: unset;
            }

        }
    }
    .modal{
        &__close{
            position: absolute;
            right: 1.5em;
            bottom: 100%;
            margin-bottom: 1.5em;
            top: unset;
        }
    }
}

.doc-modal{
    &__grid{
        display: grid;
        grid-template-columns: repeat(2,calc(50% - 3.75em));
        gap: 3em 7.5em;
        align-items: flex-start;
    }
    &__col{
        @include grid100gap(3em);
    }
    &__links{
        margin-top: 1.5em;
        padding: .75em 0 1.5em;
        border-top: $border;
        border-bottom: $border;
        display: flex;
        .m-btn{
            margin: .75em .75em 0 0;
        }
    }
}


@media screen and (max-width: 992px){
    .modal{
        display: flex;
        flex-direction: column;
        justify-content: flex-end;
        align-items: center;
        &__dialog{
            border-radius: 1em 1em 0 0;
            width: 100%;
            height: auto;
            padding: 1em;
            max-height: calc(100% - 5.25em);
            transform: translateY(120%);
        }
        &__close{
            top: 1em;
            right: 50%;
            transform: translateX(50%);
            width: 3.875em;
            height: 3.25em;
        }
        .footer__links{
            margin-top: 1em;
        }
    }

    .client-card{
        padding: 1em;
    }

    .client-gallery{
        &__grid{
            margin-top: 1em;
        }
    }

    .text-modal{
        &__num{
            margin-bottom: 1em;
        }
    }

    .modal2{
        .modal{
            &__dialog{
                overflow: unset;
            }
            &__close{
                right: 50%;
                margin-bottom: 1em;
            }
        }
    }

    .doc-modal{
        &__grid{
            grid-template-columns: 100%;
        }
        &__col{
            gap: 2em;
        }
        &__links{
            padding: .25em 0 .75em;
            margin-top: .75em;
            .m-btn{
                padding: 0 1em;
                margin: .5em .5em 0 0;
            }
        }
    }
}


@media screen and (max-width: 576px){
    .client-card{
        grid-template-columns: 100%;
        &__content{
            @include grid100gap(.5em);
        }
        &__title{
            padding: 0;
            order: -1;
        }
    }

    .client-gallery{
        &__grid{
            grid-template-columns: repeat(2,calc(50% - .4375em));
            gap: .875em;
        }
    }
}
