.blog{
  &__inner{
    border-radius: 1.5em;
    border: $border;
    padding: 2.5em;
  }
  &__top{
    display: flex;
    justify-content: space-between;
    align-items: flex-end;
  }
  &__title{
    display: flex;
    align-items: flex-start;
  }
  &__img{
    margin: -1.25em 0 0 1.25em;
    width: 6.1875em;
  }
  &__link{
    svg{
      margin-left: 1.5em;
    }
  }
  &__items{
    margin-top: 3em;
    display: grid;
    grid-template-columns: repeat(2,calc(50% - .75em));
    gap: 1.5em;
  }
}

.blog-card,
.work-card__inner{
  &:hover{
    color: $black;
    .work-card__img,
    .blog-card__img{
      img{
        transform: scale(1.2);
      }
    }
    .work-card__link,
    .blog-card__link{
      background-color: #fff;
      color: $red;
      .icon,
      .text {
        animation: move-up-alternate 0.3s forwards;
        will-change: transform;
      }
      &:before {
        height: 400%;
      }
      ._stroke{
        stroke: $red;
      }
    }
  }
}

.blog-card{
  background-color: $bg;
  border-radius: 1.5em;
  padding: 1.25em;
  display: grid;
  grid-template-columns: repeat(2,calc(50% - .75em));
  gap: 1.5em;
  &__content{
    display: flex;
    flex-direction: column;
    align-items: flex-start;
  }
  &__img{
    overflow: hidden;
    @include imgCover(89%);
    min-height: 100%;
    border-radius: 1.5em;
    img{
      transition: transform 300ms ease;
    }
  }
  &__title{
    @include truncate(5);
  }
  &__link{
    margin-top: auto;
    width: 4.25em;
    height: 4.25em;
    padding: 0;
    border-radius: 50%;
    &:before{
      background: #fff;
    }
  }
}

.work-type{
  &__grid{
    display: grid;
    grid-template-columns: calc(100% - 35.5em) 33.5em;
    gap: 2em;
  }
  &__items{
    display: grid;
    grid-template-rows: repeat(var(--cards-count), var(--card-height));
    gap: 1em 0;
  }
  &__top{
    position: sticky;
    top: 8em;
    display: flex;
  }
  &__info{
    position: relative;
  }
  &__img{
    position: absolute;
    top: .875em;
    left: 100%;
    margin-left: 1em;
    width: 9.81331em;
    transform: rotate(15deg);
  }
  &__img2{
    position: absolute;
    top: 100%;
    right: 100%;
    margin: -.5em -.5em 0 0;
    width: 3.1875em;
    transform: rotate(120deg);
  }
  &__img3{
    display: none;
  }
}

.work-card{
  position: sticky;
  top: 1em;
  &__inner{
    padding: 1.25em;
    box-shadow: 0 -.9375em 1.25em 0 rgba(0, 0, 0, 0.02);
    border-radius: 1.5em;
    background: $bg;
    min-height: 34.375em;
    display: flex;
    flex-direction: column;
    position: relative;
    &:hover{
      color: $black;
    }
  }
  &__top{
    display: flex;
    justify-content: space-between;
    align-items: flex-start;
  }
  &__title{
    line-height: 120%;
  }
  &__icon{
    width: 5em;
    flex-shrink: 0;
    margin-left: 1em;
    img{
      width: 100%;
      display: block;
    }
  }
  &__bottom{
    margin-top: auto;
    padding-top: 2em;
  }
  &__img{
    @include imgCover(65.7%);
    border-radius: 1em;
    overflow: hidden;
    img{
      transition: all .3s;
    }
  }
  &__link{
    position: absolute;
    left: 2.5em;
    bottom: 2.5em;
    width: 4.25em;
    height: 4.25em;
    padding: 0;
    border-radius: 50%;
  }
}


@media screen and (max-width: 992px){
  .blog{
    &__inner{
      padding: .75em;
      position: relative;
    }
    &__title{
      align-items: center;
    }
    &__img{
      width: 4.5625em;
      margin: 0;
    }
    &__items{
      margin-top: 2em;
      grid-template-columns: repeat(2,calc(50% - .4375em));
      gap: .875em;
    }
  }

  .blog-card{
    padding: .75em;
    grid-template-columns: 100%;
    gap: 1.5em;
    position: relative;
    &__title{
      font-size: 1.125em;
    }
    &__img{
      padding-bottom: 67%;
      min-height: unset;
    }
    &__link{
      position: absolute;
      bottom: 1.5em;
      left: 1.5em;
      z-index: 2;
    }
  }

  .work-type{
    &__grid{
      grid-template-columns: 100%;
    }
    &__img,
    &__img2{
      display: none;
    }
    &__img3{
      display: block;
      position: absolute;
      top: .5em;
      width: 4.875em;
      left: 100%;
      margin-left: 1em;
      transform: rotate(15deg);

    }
  }

  .work-card{
    &__inner{
      min-height: 27em;
      padding: .75em;
      border-radius: 1em;
    }
    &__top{
      flex-direction: column;
    }
    &__icon{
      width: 3em;
      margin: 0 0 .75em;
      order: -1;
    }
    &__img{
      border-radius: 1em;
    }
    &__link{
      bottom: 1.5em;
      left: 1.5em;
    }
  }
}


@media screen and (max-width: 576px){
  .blog{
    &__inner{
      padding-bottom: 6em;
    }
    &__link{
      position: absolute;
      bottom: .75em;
      left: 50%;
      transform: translateX(-50%);
      .icon{
        display: none;
      }
    }
    &__items{
      grid-template-columns: 100%;
    }
  }

  .blog2{
    .blog__inner{
      padding-bottom: .75em;
    }
    .blog__img{
      transform: translateX(-1em);
    }
  }
}
